// Initialization of JS chosen for all selects with class of chosen-select.
$(".chosen-select").chosen();

$('.accordion_check_box').change(function(e){
 	var accordionTarget = $(this).closest('.panel-heading').data('accordion');
	 	 console.log(accordionTarget);


	if( $(accordionTarget).hasClass('in') ){
		$(accordionTarget).slideUp('2000');
		$(accordionTarget).removeClass('in');

	}
	else{
		$(accordionTarget).slideDown('2000');
		$(accordionTarget).addClass('in');

	};

});

