Vue.directive('chosen', {
    twoWay: true,
    bind: function () {
        $(this.el)
            .chosen({
                inherit_select_classes: true,
                width: '30%',
                disable_search_threshold: 999,
                no_results_text: "<a data-toggle='modal' data-target='#create-expertise' href=''>No results found, add expertise </a> "

            })
            .change(function(ev) {
                // Grab the expertise name
                var name = $(this.el).find(':selected').data('name');
                // two-way set
                this.set({'expertise_id': this.el.value, 'title': name});
            }.bind(this));
    },
    update: function(nv, ov) {
        // note that we have to notify chosen about update
        $(this.el).trigger("chosen:updated");
    }
  });


